@tailwind base;
@tailwind components;
@tailwind utilities;

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

#employeesContainer {
    width: 100%;
    display: flex;
    justify-content: center;
}

#employeesContainer th, #employeesContainer td {
    padding: 12px 24px;
}

#employeesContainer thead {
    font-size: 12px;
    line-height: 16px;
    color: #374151;
    text-transform: uppercase;
    background-color: #f9fafb;
}

#employeesContainer table {
    width: 100%;
    font-size: 14px;
    line-height: 20px;
    text-align: left;
    color: #6b7280;
}

#employeesTitle {
    text-align: center;
    padding-top: 20px;
    margin-bottom: 50px; 
    font-size: 36px;
    line-height: 40px; 
    font-weight: 800; 
    color: #111827; 
}